<template lang="html">
<b-card id="walletCard" header-tag="header">
  <template v-slot:header>
        <h3 class="mb-0">CASH IN</h3>
  </template>
  <b-form>
      <b-form-group
        id="input-group-1"
        label-for="input-1"
        :description="'Minimum amount:' + minVal +'RUB'"
      >
      <b-input-group>
        <template v-slot:prepend>
            <span class="input-group-text"><i class="fas fa-wallet"></i></span>
        </template>
        <b-form-input
          id="input-1"
          v-model="form.amount"
          type="number"
          required
          :min="minVal"
          placeholder="Amount"
        ></b-form-input>
        
        <template v-slot:append>
            <b-dropdown :text="dropdown" variant="outline-warning">
              <b-dropdown-item>RUB</b-dropdown-item>
            </b-dropdown>
        </template>
        </b-input-group>
      </b-form-group>    
         <i class="fab fa-cc-visa"></i>
      <i class="fab fa-cc-paypal"></i>
      <i class="fab fa-cc-mastercard"></i>
      <i class="fab fa-bitcoin"></i>
      <i class="fab fa-cc-amazon-pay"></i> 
    </b-form>
</b-card>
</template>
<style>
  #walletCard {
    position: absolute;
    width: 50%;
    left: 50%;
    top: 39%;
    transform: translate(-50%, -50%);
    box-shadow: 0 4px 10px rgba(0,0,0,0.19), 0 2px 2px rgba(0,0,0,0.23);
  }
  #submitButton{
    width: 39%;
  }
  body{
    background-color: rgb(255,235,227, 0.15)!important;
  }
  #formText{
    float:right;
  }
  .input-group-text {
  border-right: none !important;
  background-color: #ffffff!important;
  color: rgb(255,193,7)!important;
  }
  #input-1{
    border-left: none !important;  
    outline: none;
    box-shadow:none !important;
    border:1px solid #ccc !important;
  }
</style>
<script>
//import axios from 'axios'
const minimum = 75;
export default {
  data () {
    return {
      form: {
          amount:'' 
        },
       dropdown: 'RUB',
       minVal: minimum 
    }
  },
  computed(){
    this.amount = this.amount < minimum ? 75 : this.amount
  }
}
</script>